import React from 'react';

export default class ListStatusItem extends React.Component {
  render() {
    let buttons = [
      {name: 'all', label: 'All'},
      {name: 'active', label: 'Active'},
      {name: 'done', label: 'Done'},
    ]

    buttons = buttons.map(({name, label}) => {
      let className = name === this.props.filter ? 'btn-primary' : "btn-outline-secondary";
      return (
        <button key={name} className={`btn ${className}`} onClick={ () => { this.props.onFilterChange(name)} }>{label}</button>
      );
    })

    return (
      <div className="btn-group">
        {buttons}
      </div>
    );
  }
}