import React from 'react';

import './app.css';

import AppHeader from '../app-header';
import TodoList from '../todo-list';
import SearchList from '../search-list';
import ListStatusItem from '../list-status-item';
import AddItem from '../add-item';

export default class App extends React.Component {

  constructor() {
    super();

    this.maxId = 0;

    let todoData;

    if(localStorage.todoQuests) {
      todoData = this.readData();
    } else {
      todoData = [
        this.createNewItem('Drink Coffee'),
        this.createNewItem('Build Awesome App'),
        this.createNewItem('Make tea')
      ];
    }

    this.state = {
      todoData,
      search: '',
      filter: 'all'
    };

    this.onDelete = this.onDelete.bind(this);
    this.onAddItem = this.onAddItem.bind(this);
    this.onToggleDone = this.onToggleDone.bind(this);
    this.onToggleImportant = this.onToggleImportant.bind(this);
    this.onSearch = this.onSearch.bind(this);
    this.onFilterChange = this.onFilterChange.bind(this);
  }

  createNewItem(text) {
    return {
      label: text,
      important: false,
      done: false,
      id: ++this.maxId,
    }
  }

  changeProperty(arr, id, prop) {
    let idx = this.state.todoData.findIndex((item) => {
      return item.id === id;
    });

    let newItem = {...arr[idx], [prop]: !arr[idx][prop]};
    let newArr = [
      ...this.state.todoData.slice(0, idx),
      newItem,
      ...this.state.todoData.slice(idx + 1)
    ];
    localStorage.todoQuests = JSON.stringify(newArr);
    return newArr
  }

  readData() {
    let items = JSON.parse(localStorage.todoQuests);
    return items.map((el) => {
      let item = {...el};
      item.id = ++this.maxId;
      return item;
    });
  }
  saveData(arr) {
    localStorage.todoQuests = JSON.stringify(arr);
  }

  onDelete(id) {
    this.setState(() => {
      let idx = this.state.todoData.findIndex((item) => {
        return item.id === id;
      });

      let newArr = [
        ...this.state.todoData.slice(0, idx),
        ...this.state.todoData.slice(idx + 1)
      ];
      this.saveData(newArr);
      return {
        todoData: newArr
      }
    });
  }

  onAddItem(text) {
    let newItem = this.createNewItem(text);

    this.setState( ( {todoData} ) => {
      let newArr = todoData.copyWithin(0);
      newArr.push(newItem);
      this.saveData(newArr);
      return {
        todoData: newArr
      }
    });

  }

  onToggleDone(id) {
    this.setState( ({todoData}) => {
      return {
        todoData: this.changeProperty(todoData, id, 'done')
      }
    });
  }

  onToggleImportant(id) {
    this.setState( ({todoData}) => {
      return {
        todoData: this.changeProperty(todoData, id, 'important')
      }
    });
  }

  onSearch(search) {
    this.setState({
      search
    })
  }

  onFilterChange(filter) {
    this.setState({ filter });
  }
  searchItems(item, search) {
    return item.label.toLowerCase().indexOf(search.toLowerCase()) >= 0;
  }
  sortItems(item, filter) {
    switch(filter) {
      case "all":
        return item;
      case "active":
        return !item.done;
      case "done":
        return item.done;
      default:
        return item;
    }
  }

  render() {
    let {todoData, search, filter} = this.state;

    let visible = todoData.filter((el) => this.searchItems(el, search)).filter((el) => this.sortItems(el, filter));

    let done = todoData.filter( (el) => el.done ).length;
    let todo = todoData.length - done;


    return (
      <div className="todo-app">
        <AppHeader toDo={todo} done={done} />
        <div className="top-panel d-flex">
          <SearchList onSearch={ this.onSearch } />
          <ListStatusItem filter={filter} onFilterChange={ this.onFilterChange } />
        </div>
        <TodoList todos={ visible }
                  onDelete={ this.onDelete }
                  onDone={ this.onToggleDone }
                  onImportant={ this.onToggleImportant }
                  />
        <AddItem onAddItem={ this.onAddItem }/>
      </div>
    );
  }
}
